import React from 'react';
import axios from 'axios';
import { Link, RouteComponentProps } from 'react-router-dom';
import { BrComponent, BrPage, BrPageContext } from '@bloomreach/react-sdk';
import { Banner, Content, Menu, NewsList } from './components';
import { Header, Footer, Promo, Container, Row, Col, Panel, Hero } from 'nhsuk-react-components';

axios.interceptors.request.use(config => ({ ...config, withCredentials: true }));

export default function App(props: RouteComponentProps) {
  const configuration = {
    httpClient: axios,
    options: {
      live: {
        cmsBaseUrl: process.env.REACT_APP_LIVE_BR_BASE_URL!,
        spaBaseUrl: process.env.REACT_APP_LIVE_SPA_BASE_URL,
      },
      preview: {
        cmsBaseUrl: process.env.REACT_APP_PREVIEW_BR_BASE_URL!,
        spaBaseUrl: process.env.REACT_APP_PREVIEW_SPA_BASE_URL,
      },
    },
    request: {
      path: `${props.location.pathname}${props.location.search}`,
    },
  };
  const mapping = { Banner, Content, 'News List': NewsList };

  return (
    <BrPage configuration={configuration} mapping={mapping}>
      <Header orgName="Health Education England" orgSplit="Library and Knowledge Services">
        <Header.Container>
            <Header.Logo href="/" />
            <Header.Content>
                <Header.MenuToggle />
                <Header.Search />
            </Header.Content>
        </Header.Container>
        <BrComponent path="menu">
          <Menu />
        </BrComponent>
    </Header>
    <Container>
      <Row>
        <Col width="full">
            <Panel>
            <BrComponent path="main" />
            </Panel>
        </Col>
      </Row>
      </Container>
      <Footer>
        <Footer.List columns>
          <Footer.ListItem href="https://www.nhs.uk/nhs-sites/">NHS sites</Footer.ListItem>
          <Footer.ListItem href="https://www.nhs.uk/about-us/">About us</Footer.ListItem>
          <Footer.ListItem href="https://www.nhs.uk/contact-us/">Contact us</Footer.ListItem>
          <Footer.ListItem href="https://www.nhs.uk/personalisation/login.aspx">
            Profile editor login
          </Footer.ListItem>
          <Footer.ListItem href="https://www.nhs.uk/about-us/sitemap/">Sitemap</Footer.ListItem>
          <Footer.ListItem href="https://www.nhs.uk/accessibility/">Accessibility</Footer.ListItem>
          <Footer.ListItem href="https://www.nhs.uk/our-policies/">Our policies</Footer.ListItem>
          <Footer.ListItem href="https://www.nhs.uk/our-policies/cookies-policy/">
            Cookies
          </Footer.ListItem>
        </Footer.List>
        <Footer.Copyright>&copy; Crown copyright</Footer.Copyright>
    </Footer>
    </BrPage>
  );
}
